/**
 * @description 上传文件
 *
 */

// import Uppy, { UppyFile } from '@uppy/core';
import { IDomEditor, createUploader } from '@klein-editor/core';
import { insertImageNode } from '@klein-editor/basic-modules';
import { IUploadConfigForImage } from './menu/config';

// 存储 editor 与 instance 的关系 - 缓存 instance ，不重复创建
const EDITOR_TO_UPPY_MAP = new WeakMap<IDomEditor, any>();

/**
 * 获取 instance 实例（并通过 editor 缓存）
 * @param editor editor
 */
function getInstance(editor: IDomEditor): any {
  // 从缓存中获取
  let instance = EDITOR_TO_UPPY_MAP.get(editor);
  if (instance != null) return instance;

  const menuConfig = getMenuConfig(editor);

  // progress 显示进度条
  const progressHandler = (progress: number) => {
    editor.showProgressBar(progress);
  };

  // 创建 instance
  instance = createUploader({
    ...menuConfig,
    onProgress: (file, state, progress) => {
      const percent = progress?.percent || 0;
      progressHandler(percent);
    },
  });

  // 缓存 instance
  EDITOR_TO_UPPY_MAP.set(editor, instance);

  return instance;
}

function getMenuConfig(editor: IDomEditor) {
  return editor.getMenuConfig('uploadImage') as IUploadConfigForImage;
}

/**
 * 插入 base64 格式
 * @param editor editor
 * @param file file
 */
async function insertBase64(editor: IDomEditor, file: File) {
  return new Promise((resolve) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
      const { result } = reader;
      if (!result) return;
      const src = result.toString();
      let href = src.indexOf('data:image') === 0 ? '' : src; // base64 格式则不设置 href
      insertImageNode(editor, src, file.name, href);

      resolve('ok');
    };
  });
}

/**
 * 上传图片文件
 * @param editor editor
 * @param file file
 */
async function uploadFile(editor: IDomEditor, file: File) {
  const menuConfig = getMenuConfig(editor);
  const { customInsert } = menuConfig;

  // 上传完成之后
  const successHandler = (file: File, res: any) => {
    if (customInsert) {
      // 用户自定义插入图片，此时 res 格式可能不符合预期
      customInsert(res, (src, alt, href) =>
        insertImageNode(editor, src, alt, href),
      );
      return;
    }

    // 返回的对象
    const { url = '', name = '' } = res;
    insertImageNode(editor, url, name);
  };

  // onError 提示错误
  const errorHandler = (file: any, err: any) => {
    const fileName = file.name;
    console.error(`'${fileName} upload error`, err);
  };

  const uploader = getInstance(editor);
  uploader(file)
    .then((res: any) => {
      console.log('upload file res:', res);
      if (!res) return;
      if (res.errorInfo) {
        errorHandler(file, res.errorInfo);
      } else {
        successHandler(file, res);
      }
    })
    .catch((err: any) => {
      err.message = err.message || '文件上传sdk异常。';
      errorHandler(file, err);
    });
}

/**
 * 上传图片
 * @param editor editor
 * @param files files
 */
export default async function (editor: IDomEditor, files: FileList | null) {
  if (files == null) return;
  const fileList = Array.prototype.slice.call(files);

  // 获取菜单配置
  const { customUpload, base64LimitSize } = getMenuConfig(editor);

  // 按顺序上传
  for await (const file of fileList) {
    const size = file.size; // size kb
    if (base64LimitSize && size <= base64LimitSize) {
      // 允许 base64 ，而且 size 在 base64 限制之内，则插入 base64 格式
      await insertBase64(editor, file);
    } else {
      // 上传
      if (customUpload) {
        // 自定义上传
        await customUpload(file, (src, alt, href) =>
          insertImageNode(editor, src, alt, href),
        );
      } else {
        // 默认上传
        await uploadFile(editor, file);
      }
    }
  }
}
